<template>
  <div>
    <h1>Netflix 영화 순위</h1>
    <ul>
      <li v-for="movie in movies" :key="movie.pk">
        {{ movie.fields.title }}
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  data() {
    return {
      movies: [],
    };
  },
  mounted() {
    this.fetchNetflixMovies();
  },
  methods: {
    fetchNetflixMovies() {
      // 백엔드로 요청을 보내는 부분
      axios.get('/api/netflix')
        .then(response => {
          this.movies = response.data;
        })
        .catch(error => {
          console.error('데이터를 가져오는 중 오류가 발생했습니다:', error);
        });
    },
  },
};
</script>
